.middle{
    width: 100%;
}

.Header{
    display: flex;
    align-items: center;
    gap: 1rem;   
}
.Header h1{
    color: var(--color-dark);
} 
.Header input[type="date"]{
    padding: 0.5rem 2rem;
    border-radius: var(--border-radius-2);
    background: var(--color-white);
    color: var(--color-gray-dark);
}
.cards{
   
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:1.5rem;
}
.cards .card{
    background: linear-gradient(#ff796f,#bd261b);
    padding: var(--card-padding);
    border-radius: var(--card-border-radius);
    color: var(--color-white);
    height: 16rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 2rem 3rem var(--color-danger-light);
    transition: all 300ms ease;
    
}

.cards .card:nth-child(2){
    background: linear-gradient(#7f8191,#27282f);
    box-shadow: 0 2rem 3rem rgba(0, 0, 0, 0.2);
}
.cards .card:nth-child(3){
    background: linear-gradient(#5d70ff,#5719c2);
    box-shadow: 0 2rem 3rem var(--color-primary-light);
}
.cards .card:hover{
    box-shadow: none;
}
.card .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.top .left{
    display: flex;
    gap:0.5rem;
}
.top .left h2{
    font-weight: 200;
    font-size: 1.4rem;

}
.top .left img{
    width: 2.3rem;
    height: 2.3rem;
    border: 1px solid white;
    border-radius: var(--border-radius-2);
    padding: 0.3rem;
}
.report h3{
    color: var(--color-dark);
}
 .report div details h1{
    color: var(--color-dark);
}
.report div details{
    color: var(--color-dark);
}
.fast-payment h2{
    color: var(--color-dark);
}
.badge div{
    color: var(--color-dark);
}
.badge span{
    color: var(--color-dark);
}
.card .top .right{
    width: 3.5rem;
}
.card .middle{
  display: flex;
  justify-content: space-between;
}
.middle .chip{
    width: 3.5rem;
}
.card .bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.card .bottom .right{
    display: flex;
    gap: 2rem;
}
.monthly-report{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap:1rem;
    justify-content: space-between;
    margin-top: 2rem;
}
.monthly-report h1{
    font-weight: 700;
    font-size: 1.8rem;


}
.fast-payment{
    margin-top: 2rem;
    display: flex;
    align-self: center;
    gap: 2rem;
}
.fast-payment .badges{
    display: flex;
    gap:1rem;
    align-self: center;
    flex-wrap: wrap;
    max-width: 100%;
}
.fast-payment .badge span{
    width: 7px;
    height: 7px;
    border-radius: 50%;
}
.fast-payment .badge{
    padding: 0.6rem 2rem;
    background: var(--color-white);
    border-radius: var(--border-radius-2);
    display: flex;
    align-items: center;
    gap: 1rem;
    transition: all 300ms ease;
}
.fast-payment .badge:hover{
    cursor: pointer;
    box-shadow: 0 0 1.5rem var(--color-primary-light);
}
.fast-payment .badge:first-child span{
    display: flex;
    align-self: center;
    justify-content: center;
    height: fit-content;
    font-size: 1.5rem;
}
.fast-payment .badge div{
    display: flex;
    gap: 2rem;
    align-self: center;
}
.chart{
    background: var(--color-white);
    height: 450px;
    margin-top: 2rem;
    border-radius: var(--card-border-radius);
    padding: var(--card-padding);
}

@media screen and (max-width:1024px) {
   .middle .cards{
    grid-template-columns: 1fr 1fr;
   }
   .card{
    width: 100%;
    height: 100%;
   }
   .chart{
    margin: 3rem 0 1rem;
   }
}
@media screen and (max-width:600px) {
  .fast-payment{
    flex-direction: column;
    align-items: flex-start;
    margin-top: 3rem;
    gap: 1rem;

  }
  
  .middle .cards{
    grid-template-columns: 1fr;
  }
}