.investments{
    background: var(--color-white);
    border-radius: var(--card-border-radius);
}
.investments img{
    width: 2.4rem;
}
.investments .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--card-padding);
    padding-bottom: 0;
}
.investments .header a{
    display: flex;
    align-items: center;
    color: var(--color-primary);
}
.investment h4{
    color: var(--color-dark);
}
.recent-transactions .header h2{
    color: var(--color-dark);
}
.transaction .service .details h4{
    color: var(--color-dark);
}
.investments .header h1{
    color: var(--color-dark);
}
.investments .investment{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--card-padding);
    border-bottom: 1px solid var(--color-light);
    transition: all 300ms ease;

}

.investments .investment:hover{
    background: var(--color-light);
    cursor: pointer;
}

.recent-transactions{
    margin-top: 2rem;

}
.recent-transactions .header{
    display: flex;
    margin-bottom: 1rem;
    justify-content: space-between;
}
.recent-transactions .header a{
    display: flex;
    align-items: center;
    color: var(--color-primary);

}

.recent-transactions img{
    width: 2.2rem;
}
.recent-transactions .transaction{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem var(--card-padding);
    border-radius: var(--card-border-radius);
    transition: all 300ms ease;
}
.recent-transactions .transaction:hover{
    background: var(--color-white);
    cursor: pointer;
}
.recent-transactions .transaction .service{
    display: flex;
    gap: 1rem;

}
.recent-transactions .transaction .service .icon{
    padding: var(--padding-2);
    border-radius: var(--border-radius-1);
    display: flex;
    align-items: center;

}
.recent-transactions .card-details{
    display: flex;
    align-items: center;
    gap: 1rem; 
}
.recent-transactions .card-details .card{
    display: flex;
    width: 4.6rem;
    height: 3rem;
    align-items: center;
    justify-content: center;
    border-radius: var(--border-radius-2);

}
@media screen and (max-width:1024px) {
    .recent-transactions{
        margin-top: 3rem;
    }
}
