@import "./CSSs/root.css";
*{
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  appearance: none;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
html{
  font-size: 12px;
}
body {
  min-height: 100vh;

font-family: poppins, sans-serif;
color: var(--color-dark);
background: var(--color-light);
}
.material-symbols-sharp {
  font-variation-settings:
  'FILL' 1,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}

h1{
  font-size: 2.2rem;
}
h2{
  font-size: 1.5rem;
}
h3{
  font-size: 1.2rem;
}
h4{
  font-size: 0.86rem;
  font-weight: 500;
}
h6{
  font-size: 0.76rem;
}
p{
  font-size: 0.86rem;
  color: var(--color-gray-dark);
}
small{
  font-weight: 300;
  font-size: 0.77rem;
}
.text-muted{
  color: var(--color-dark);
}
.primary{
  color: var(--color-primary);
}
.danger{
  color: var(--color-danger);
}
.success{
  color: var(--color-success);
}
.purple{
  color: var(--color-purple);
}
.bg-primary{
  background: var(--color-primary);
  box-shadow: 0 0.8rem 0.8rem var(--color-primary-light);
}
.bg-danger{
  background: var(--color-danger);
  box-shadow: 0 0.8rem 0.8rem var(--color-danger-light);
}
.bg-success{
  background: var(--color-success);
  box-shadow: 0 0.8rem 0.8rem var(--color-success-light);
}
.bg-purple{
  background: var(--color-purple);
  box-shadow: 0 0.8rem 0.8rem var(--color-purple-light);
}
.bg-dark{
  background: var(--color-dark);
  box-shadow: 0 0.8rem 0.8rem rgba(0,0,0,0.2);
}




.bg-primary-light{
  background: var(--color-primary-light);
 
}
.bg-danger-light{
  background: var(--color-danger-light);
}
.bg-success-light{
  background: var(--color-success-light);
}
.bg-purple-light{
  background: var(--color-purple-light);
}