

nav{
    width: 100%;
    background: var(--color-white);
    padding: 1rem 0;
}
.profile h5,.profile span{
    color: var(--color-dark);
} 
.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 96%;
    margin: 0 auto;
}
.logo{
    width: 7rem;    
}
.search-bar{
    background: var(--color-light);
    padding: var(--padding-2) var(--card-padding);
    width: 32vw;
    border-radius: var(--border-radius-2);
    display: flex;
    align-items: center;
    gap:1rem;
    color: var(--color-gray-light);
    position: absolute;
    left: 15%;
}

.search-bar input[type='search']{
  color: var(--color-dark);
  background: transparent;
  width: 100%;
}
.search-bar input[type='search']::placeholder{
    color: var(--color-gray-light);
    
}
.profile-area{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:4rem
}
.theme-btn{
  
    display: flex;
    background: var(--color-light);
    width: 5rem;
    height: 2.3rem;
    border-radius: var(--border-radius-2);
    cursor: pointer;
}
.theme-btn span{
   
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
}
.theme-btn .active{
    
    background: var(--color-dark);
    border-radius: var(--border-radius-2);
    color: var(--color-white);
}
.profile{
    display: flex;
    gap: 1rem;
    align-items: center;
}
.profile-photo{
    display: block;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    overflow: hidden;
}
.profile-photo img{
    width: inherit;
    height: inherit;
}
.profile-area button{
    display: none;
}
@media screen and (max-width:1024px) {
    .search-bar{
        display: none;
    }
    .profile-area{
        gap: 2rem;
    }
    .profile-area button{
        display: inline;
        background: transparent;
        color: var(--color-dark);
    }
    
}
@media screen and (max-width:600px) {
    .profile-area{
        gap:2.6rem;
    }
    .profile h5,.profile span{
        display: none;
    }
}