.side{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 88vh;
  
}
.side .close-btn{
    display: none;
}
.sidebar a{
    display: flex;
    align-items: center;
    gap:1.2rem;
    height: 4.2rem;
    color: var(--color-gray-light);
    position: relative;
}
.sidebar a span{
    font-size: 1.7rem;
    margin-left: 3rem;
    transition: all 300ms ease;

}
.sidebar a.active{
    background: var(--color-white);
    color: var(--color-primary);
}
.sidebar a.active::before{
    content: "";
    width: 6px;
    height: 100%;
    position: absolute;
    background: var(--color-primary);
    
}
.sidebar a:hover{
    color: var(--color-primary);
}
.sidebar a:hover span{
    margin-left: 2rem;
}
.sidebar h4{
    font-weight: 600;
}
.updates{
    background: var(--color-white);
    border-radius: var(--border-radius-1);
    text-align: center;
    padding: var(--card-padding);
}
.updates span{
    font-size: 2.8rem;
}
.updates h4{
    margin:1rem 0;
}
.updates a{
    display: block;
    width: 100%;
    background: var(--color-primary);
    color: var(--color-white);
    border-radius: var(--border-radius-1);
    padding:  0.8rem 0;
    margin-top: 2rem;
    transition: all 300ms ease;
}
.updates a:hover{
    box-shadow: 0 1rem 2rem var(--color-primary-light);
}
.close-btn span{
    color: var(--color-dark);
}
@media screen and (max-width:1024px) {
    main .side{
        background: var(--color-light);
        position: fixed;
        top: 0;
        left: -25rem;
        z-index: 3;
        width: 22rem;
        height: 100vh;
        box-shadow: 2rem 0 2rem var(--color-primary-light);
        transition: all 350ms ease-in-out;
    }
    main .side.open{
        left:0;
    }
    .side .close-btn{
        display: inline-block;
        width: 3rem;
        height: 3rem;
        position: absolute;
        top: 1rem;
        right: 1rem;
        z-index: 4;
        background: transparent;  
    }
    button:hover{
        cursor: pointer;
    }
    aside .sidebar{
        margin-top: 4rem;

    }
    aside .updates{
        display: none;
    }
}